<template>
  <div class="about">
    <div class="mousemove" @mousemove="debounceInput">
      <h1>{{ num }}</h1>
    </div>
    <div class="button-click">
      <button @click="bounceClick">点我</button>
      <h1>{{ buttonNum }}</h1>
    </div>
  </div>
</template>
<script>
import { debounce, throttle } from '../utils/debounce'
export default {
  name: 'about',
  data() {
    return {
      num: 0,
      buttonNum: 0,
      debounceInput: debounce(this.count, 300),
      bounceClick: throttle(this.buttonCount, 1000)
    }
  },
  methods: {
    count() {
      this.num++
    },
    buttonCount() {
      this.buttonNum++
    }
  }
}
</script>
<style lang="scss" scoped>
.about {
  flex-grow: 1;
}
.mousemove {
  height: 250px;
  border: 1px dashed;
}
.button-click {
  height: 250px;
  border: 1px dashed;
}
</style>