export function debounce(func, delay = 300, immediate = false) {
  let timer = null;
  return function () {
    if (immediate && !timer) func.apply(this, arguments);
    clearTimeout(timer);
    timer = setTimeout(() => {
      if (!immediate) {
        func.apply(this, arguments);
      } else {
        timer = null;
      }
    }, delay);
  };
}
export function throttle(func, delay = 1000) {
  let valid = true;
  return function () {
    if (valid) {
      func.apply(this, arguments);
      valid = false;
      setTimeout(() => {
        valid = true;
      }, delay);
    }
  };
}
